import React, { useState } from 'react'
import {
  Box,
  Card,
  CardContent,
  Typography,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Alert,
  Snackbar,
  Grid,
  Divider,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton
} from '@mui/material'
import {
  Delete as DeleteIcon,
  DeleteSweep as DeleteSweepIcon,
  Warning as WarningIcon
} from '@mui/icons-material'
import { clearAllData, clearMachineData, fetchMachines } from '../services/api'

const AdminPanel = () => {
  const [machines, setMachines] = useState([])
  const [loading, setLoading] = useState(false)
  const [openClearAll, setOpenClearAll] = useState(false)
  const [openClearMachine, setOpenClearMachine] = useState(false)
  const [selectedMachine, setSelectedMachine] = useState('')
  const [confirmText, setConfirmText] = useState('')
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' })

  // Cargar lista de mquinas
  const loadMachines = async () => {
    try {
      setLoading(true)
      const data = await fetchMachines()
      setMachines(data.machines || [])
    } catch (error) {
      showSnackbar('Error cargando mquinas: ' + error.message, 'error')
    } finally {
      setLoading(false)
    }
  }

  // Mostrar notificacin
  const showSnackbar = (message, severity = 'success') => {
    setSnackbar({ open: true, message, severity })
  }

  // Limpiar todos los datos
  const handleClearAll = async () => {
    if (confirmText !== 'ELIMINAR TODO') {
      showSnackbar('Debes escribir "ELIMINAR TODO" para confirmar', 'error')
      return
    }

    try {
      setLoading(true)
      const result = await clearAllData()
      showSnackbar(` ${result.message}. Registros eliminados: ${result.deleted_records}`, 'success')
      setOpenClearAll(false)
      setConfirmText('')
      loadMachines() // Recargar lista
    } catch (error) {
      showSnackbar('Error eliminando datos: ' + error.message, 'error')
    } finally {
      setLoading(false)
    }
  }

  // Limpiar datos de mquina especfica
  const handleClearMachine = async () => {
    if (confirmText !== 'ELIMINAR MAQUINA') {
      showSnackbar('Debes escribir "ELIMINAR MAQUINA" para confirmar', 'error')
      return
    }

    try {
      setLoading(true)
      const result = await clearMachineData(selectedMachine)
      showSnackbar(` ${result.message}. Registros eliminados: ${result.deleted_records}`, 'success')
      setOpenClearMachine(false)
      setConfirmText('')
      setSelectedMachine('')
      loadMachines() // Recargar lista
    } catch (error) {
      showSnackbar('Error eliminando datos: ' + error.message, 'error')
    } finally {
      setLoading(false)
    }
  }

  // Abrir dialog para limpiar mquina especfica
  const openClearMachineDialog = (machineName) => {
    setSelectedMachine(machineName)
    setOpenClearMachine(true)
  }

  React.useEffect(() => {
    loadMachines()
  }, [])

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" gutterBottom color="error">
         Panel de Administracin
      </Typography>
      
      <Alert severity="warning" sx={{ mb: 3 }}>
        <strong>ADVERTENCIA:</strong> Las operaciones de eliminacin son irreversibles. 
        salas con extrema precaucin.
      </Alert>

      <Grid container spacing={3}>
        {/* Acciones globales */}
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom color="error">
                 Limpiar Base de Datos Completa
              </Typography>
              <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                Elimina TODAS las ubicaciones de TODAS las mquinas. Esta accin no se puede deshacer.
              </Typography>
              <Button
                variant="contained"
                color="error"
                startIcon={<DeleteSweepIcon />}
                onClick={() => setOpenClearAll(true)}
                disabled={loading}
                fullWidth
              >
                Limpiar Toda la Base de Datos
              </Button>
            </CardContent>
          </Card>
        </Grid>

        {/* Lista de mquinas */}
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                 Mquinas Registradas
              </Typography>
              <Button
                variant="outlined"
                onClick={loadMachines}
                disabled={loading}
                sx={{ mb: 2 }}
              >
                Recargar Lista
              </Button>
              
              {machines.length > 0 ? (
                <List dense>
                  {machines.map((machine) => (
                    <React.Fragment key={machine.machine_name}>
                      <ListItem>
                        <ListItemText
                          primary={machine.machine_name}
                          secondary={`${machine.count} ubicaciones  ltima vez: ${new Date(machine.last_seen).toLocaleString()}`}
                        />
                        <ListItemSecondaryAction>
                          <IconButton
                            edge="end"
                            color="error"
                            onClick={() => openClearMachineDialog(machine.machine_name)}
                            title={`Eliminar datos de ${machine.machine_name}`}
                          >
                            <DeleteIcon />
                          </IconButton>
                        </ListItemSecondaryAction>
                      </ListItem>
                      <Divider />
                    </React.Fragment>
                  ))}
                </List>
              ) : (
                <Typography variant="body2" color="text.secondary">
                  No hay mquinas registradas o estn cargando...
                </Typography>
              )}
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Dialog: Limpiar toda la base de datos */}
      <Dialog open={openClearAll} onClose={() => setOpenClearAll(false)} maxWidth="sm" fullWidth>
        <DialogTitle>
          <Box display="flex" alignItems="center">
            <WarningIcon color="error" sx={{ mr: 1 }} />
            Confirmar Eliminacin Total
          </Box>
        </DialogTitle>
        <DialogContent>
          <Alert severity="error" sx={{ mb: 2 }}>
            Vas a eliminar TODAS las ubicaciones de TODAS las mquinas.
            Esta accin es IRREVERSIBLE.
          </Alert>
          <Typography variant="body1" sx={{ mb: 2 }}>
            Para confirmar, escribe exactamente: <strong>ELIMINAR TODO</strong>
          </Typography>
          <TextField
            fullWidth
            label="Confirmacin"
            value={confirmText}
            onChange={(e) => setConfirmText(e.target.value)}
            placeholder="ELIMINAR TODO"
            error={confirmText !== '' && confirmText !== 'ELIMINAR TODO'}
            helperText={confirmText !== '' && confirmText !== 'ELIMINAR TODO' ? 'Texto incorrecto' : ''}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => { setOpenClearAll(false); setConfirmText('') }}>
            Cancelar
          </Button>
          <Button
            onClick={handleClearAll}
            color="error"
            variant="contained"
            disabled={loading || confirmText !== 'ELIMINAR TODO'}
          >
            {loading ? 'Eliminando...' : 'Eliminar Todo'}
          </Button>
        </DialogActions>
      </Dialog>

      {/* Dialog: Limpiar mquina especfica */}
      <Dialog open={openClearMachine} onClose={() => setOpenClearMachine(false)} maxWidth="sm" fullWidth>
        <DialogTitle>
          <Box display="flex" alignItems="center">
            <WarningIcon color="error" sx={{ mr: 1 }} />
            Eliminar Datos de Mquina
          </Box>
        </DialogTitle>
        <DialogContent>
          <Alert severity="warning" sx={{ mb: 2 }}>
            Vas a eliminar todas las ubicaciones de: <strong>{selectedMachine}</strong>
          </Alert>
          <Typography variant="body1" sx={{ mb: 2 }}>
            Para confirmar, escribe exactamente: <strong>ELIMINAR MAQUINA</strong>
          </Typography>
          <TextField
            fullWidth
            label="Confirmacin"
            value={confirmText}
            onChange={(e) => setConfirmText(e.target.value)}
            placeholder="ELIMINAR MAQUINA"
            error={confirmText !== '' && confirmText !== 'ELIMINAR MAQUINA'}
            helperText={confirmText !== '' && confirmText !== 'ELIMINAR MAQUINA' ? 'Texto incorrecto' : ''}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => { setOpenClearMachine(false); setConfirmText(''); setSelectedMachine('') }}>
            Cancelar
          </Button>
          <Button
            onClick={handleClearMachine}
            color="error"
            variant="contained"
            disabled={loading || confirmText !== 'ELIMINAR MAQUINA'}
          >
            {loading ? 'Eliminando...' : 'Eliminar Mquina'}
          </Button>
        </DialogActions>
      </Dialog>

      {/* Snackbar para notificaciones */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
      >
        <Alert
          onClose={() => setSnackbar({ ...snackbar, open: false })}
          severity={snackbar.severity}
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  )
}

export default AdminPanel